@font-face {
  font-family: 'context-menu-icons';
  src: url("font/context-menu-icons.eot?-pa8qvz");
  src: url("font/context-menu-icons.eot?#iefix-pa8qvz") format("embedded-opentype"), url("font/context-menu-icons.woff2?-pa8qvz") format("woff2"), url("font/context-menu-icons.woff?-pa8qvz") format("woff"), url("font/context-menu-icons.ttf?-pa8qvz") format("truetype");
  font-weight: normal;
  font-style: normal; }

.context-menu-list {
  background: #FFF;
  border: 1px solid #bebebe;
  border-radius: 3px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  font-family: inherit;
  font-size: inherit;
  display: inline-block;
  list-style-type: none;
  margin: 5px;
  max-width: 360px;
  min-width: 180px;
  padding: 4px 0;
  position: absolute; }

.context-menu-item {
  background-color: #FFF;
  color: #2F2F2F;
  padding: 2px 24px;
  position: relative;
  user-select: none; }

.context-menu-separator {
  border-bottom: 1px solid #e6e6e6;
  margin: 5px 0;
  padding: 0; }

.context-menu-item > label > input,
.context-menu-item > label > textarea {
  user-select: text; }

.context-menu-item.context-menu-hover {
  background-color: #2980B9;
  color: #FFF;
  cursor: pointer; }

.context-menu-item.context-menu-disabled {
  background-color: #FFF;
  color: #626262; }

.context-menu-input.context-menu-hover,
.context-menu-item.context-menu-disabled.context-menu-hover {
  background-color: #EEE;
  cursor: default; }

.context-menu-submenu:after {
  content: '';
  border-style: solid;
  border-width: 4px 0 4px 4px;
  border-color: transparent transparent transparent #2F2F2F;
  height: 0;
  position: absolute;
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
  width: 0;
  z-index: 1; }

